import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  Container,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Button,
  TextField,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  MenuItem,
  Select,
  InputLabel,
  FormControl,
  IconButton,
  Box,
  Divider,
  InputAdornment,
  LinearProgress,
  Snackbar,
  Alert,
} from "@mui/material";
import { DateTimePicker } from "@mui/x-date-pickers";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { Add, Edit, Delete, Search } from "@mui/icons-material";
import { useParams } from "react-router-dom";
import dayjs from "dayjs";
import AddCircleIcon from "@mui/icons-material/AddCircle";

const ProductMovements = () => {
  const { productId } = useParams();

  const [movements, setMovements] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [loading, setLoading] = useState(false);
  const [openDialog, setOpenDialog] = useState(false);
  const [movementForm, setMovementForm] = useState({
    datetime: dayjs(),
    type: "entree",
    quantity: "",
    location: "armoire 01",
    operator: "",
  });
  const [editingMovement, setEditingMovement] = useState(null);

  const [errorMessage, setErrorMessage] = useState(""); // Error message state
  const [successMessage, setSuccessMessage] = useState(""); // Success message state
  const [openSnackbar, setOpenSnackbar] = useState(false); // Snackbar open state

  useEffect(() => {
    setLoading(true);
    axios
      .get(`http://192.168.1.40/api/mvt/product/${productId}`)
      .then((response) => {
        setMovements(response.data);
        setLoading(false);
      })
      .catch((error) => {
        console.error("Error fetching product movements:", error);
        setLoading(false);
        setErrorMessage("Une erreur s'est produite lors du chargement des mouvements.");
        setOpenSnackbar(true);
      });
  }, [productId]);

  const handleOpenDialog = (movement = null) => {
    if (movement) {
      setEditingMovement(movement);
      setMovementForm({
        datetime: dayjs(movement.datetime),
        type: movement.type,
        quantity: movement.quantity,
        location: movement.location,
        operator: movement.operator,
      });
    } else {
      setEditingMovement(null);
      setMovementForm({
        datetime: dayjs(),
        type: "entree",
        quantity: "",
        location: "armoire 01",
        operator: "",
      });
    }
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
  };

  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setMovementForm({ ...movementForm, [name]: value });
  };

  const handleDateChange = (newValue) => {
    setMovementForm({ ...movementForm, datetime: newValue });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const formWithParsedData = {
      ...movementForm,
      datetime: movementForm.datetime.toISOString(),
      quantity: parseFloat(movementForm.quantity) || 0,
      product: productId,
    };

    if (editingMovement) {
      axios
        .put(`http://192.168.1.40/api/mvt/${editingMovement._id}`, formWithParsedData)
        .then(() => {
          setMovements((prev) =>
            prev.map((mv) => (mv._id === editingMovement._id ? { ...mv, ...formWithParsedData } : mv))
          );
          setSuccessMessage("Mouvement modifi avec succs.");
          setOpenSnackbar(true);
          setOpenDialog(false);
        })
        .catch((error) => {
          console.error("Error updating movement:", error);
          setErrorMessage("Une erreur s'est produite lors de la mise  jour du mouvement.");
          setOpenSnackbar(true);
        });
    } else {
      axios
        .post(`http://192.168.1.40/api/mvt`, formWithParsedData)
        .then((response) => {
          setMovements((prev) => [...prev, response.data]);
          setSuccessMessage("Mouvement ajout avec succs.");
          setOpenSnackbar(true);
          setOpenDialog(false);
        })
        .catch((error) => {
          console.error("Error adding movement:", error);
          setErrorMessage("Une erreur s'est produite lors de l'ajout du mouvement.");
          setOpenSnackbar(true);
        });
    }
  };

  const handleDelete = (id) => {
    axios
      .delete(`http://192.168.1.40/api/mvt/${id}`)
      .then(() => {
        setMovements((prev) => prev.filter((mv) => mv._id !== id));
        setSuccessMessage("Mouvement supprim avec succs.");
        setOpenSnackbar(true);
      })
      .catch((error) => {
        console.error("Error deleting movement:", error);
        setErrorMessage("Une erreur s'est produite lors de la suppression du mouvement.");
        setOpenSnackbar(true);
      });
  };

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <Container maxWidth="lg">
        <Typography
          variant="h4"
          gutterBottom
          align="center"
          sx={{
            marginBottom: "30px",
            fontWeight: "600",
            color: "#333",
            fontFamily: "Roboto, sans-serif",
          }}
        >
          Gestion des Mouvements de Produit "{movements.length > 0 && movements[0].product?.name ? movements[0].product.name : ""}"
        </Typography>

        {/* Search Bar */}
        <Box display="flex" justifyContent="space-between" alignItems="center" marginBottom={3}>
          <TextField
            label="Rechercher"
            variant="outlined"
            fullWidth
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            sx={{
              maxWidth: "600px",
              backgroundColor: "#f4f6f8",
              borderRadius: "12px",
            }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Search />
                </InputAdornment>
              ),
            }}
          />
          <IconButton
            variant="contained"
            color="primary"
            startIcon={<Add />}
            onClick={() => handleOpenDialog()}
            sx={{
              bgcolor: "primary.main",
              color: "white",
              "&:hover": { bgcolor: "primary.dark" },
              borderRadius: "50%",
              padding: "12px",
            }}
          >
            <AddCircleIcon fontSize="large" />
          </IconButton>
        </Box>

        {/* Loading Progress */}
        {loading && <LinearProgress sx={{ marginBottom: "20px" }} />}

        {/* Table of Movements */}
        <TableContainer
          component={Paper}
          sx={{
            borderRadius: "12px",
            boxShadow: 2,
            overflow: "hidden",
            marginBottom: "20px",
          }}
        >
          <Table>
            <TableHead>
              <TableRow>
                {["Date/Heure", "Type", "Quantit", "Localisation", "Oprateur", "Actions"].map((header) => (
                  <TableCell
                    key={header}
                    align="center"
                    sx={{
                      fontWeight: "bold",
                      backgroundColor: "#1976d2",
                      color: "white",
                      fontSize: "16px",
                      borderBottom: "2px solid #e0e0e0",
                    }}
                  >
                    {header}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {movements
                .filter((movement) =>
                  movement.type.includes(searchTerm) || movement.location.includes(searchTerm)
                )
                .map((movement) => (
                  <TableRow
                    key={movement._id}
                    sx={{
                      backgroundColor: movement.type === "entree" ? "#e8f5e9" : "#fce4ec",
                      "&:hover": { backgroundColor: "#f1f1f1" },
                    }}
                  >
                    <TableCell align="center">{dayjs(movement.datetime).format("DD/MM/YYYY HH:mm")}</TableCell>
                    <TableCell align="center">{movement.type}</TableCell>
                    <TableCell align="center">{movement.quantity}</TableCell>
                    <TableCell align="center">{movement.location}</TableCell>
                    <TableCell align="center">{movement.operator}</TableCell>
                    <TableCell align="center">
                      <IconButton onClick={() => handleOpenDialog(movement)} color="primary">
                        <Edit />
                      </IconButton>
                      <IconButton onClick={() => handleDelete(movement._id)} color="secondary">
                        <Delete />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
        </TableContainer>

        {/* Dialog for Add/Edit Movement */}
        <Dialog open={openDialog} onClose={handleCloseDialog}>
          <DialogTitle>{editingMovement ? "Modifier le Mouvements" : "Ajouter un Mouvements"}</DialogTitle>
          <DialogContent>
            <DateTimePicker
              label="Date"
              value={movementForm.datetime}
              onChange={handleDateChange}
              fullWidth
              sx={{ marginBottom: "15px" }}
            />
            <FormControl fullWidth sx={{ marginBottom: "15px" }}>
              <InputLabel>Type</InputLabel>
              <Select
                name="type"
                value={movementForm.type}
                onChange={handleFormChange}
                label="Type"
              >
                <MenuItem value="entree">Entre</MenuItem>
                <MenuItem value="sortie">Sortie</MenuItem>
              </Select>
            </FormControl>
            <TextField
              label="Quantit"
              name="quantity"
              type="number"
              value={movementForm.quantity}
              onChange={handleFormChange}
              fullWidth
              sx={{ marginBottom: "15px" }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">#</InputAdornment>
                ),
              }}
            />
            <FormControl fullWidth sx={{ marginBottom: "15px" }}>
              <InputLabel>Localisation</InputLabel>
              <Select
                name="location"
                value={movementForm.location}
                onChange={handleFormChange}
                label="Localisation"
              >
                <MenuItem value="armoire 01">Armoires 01</MenuItem>
                <MenuItem value="armoire 02">Armoires 02</MenuItem>
                <MenuItem value="armoire 03">Armoires 03</MenuItem>
              </Select>
            </FormControl>
            <TextField
              label="Oprateur"
              name="operator"
              value={movementForm.operator}
              onChange={handleFormChange}
              fullWidth
              sx={{ marginBottom: "15px" }}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDialog} color="secondary">
              Annuler
            </Button>
            <Button onClick={handleSubmit} color="primary">
              {editingMovement ? "Sauvegarder" : "Ajouter"}
            </Button>
          </DialogActions>
        </Dialog>

        {/* Snackbar for error/success messages */}
        <Snackbar
          open={openSnackbar}
          autoHideDuration={6000}
          onClose={() => setOpenSnackbar(false)}
        >
          <Alert severity={errorMessage ? "error" : "success"} onClose={() => setOpenSnackbar(false)}>
            {errorMessage || successMessage}
          </Alert>
        </Snackbar>
      </Container>
    </LocalizationProvider>
  );
};

export default ProductMovements;
