import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  Container,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  TextField,
  Box,
  Tooltip,
} from "@mui/material";
import { Link, useNavigate } from "react-router-dom";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import VisibilityIcon from "@mui/icons-material/Visibility";

const ProductList = () => {
  const [products, setProducts] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [filteredProducts, setFilteredProducts] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    axios
      .get("http://192.168.1.40/api/products")
      .then((response) => {
        setProducts(response.data);
        setFilteredProducts(response.data);
      })
      .catch((error) => console.error(error));
  }, []);

  useEffect(() => {
    setFilteredProducts(
      products.filter(
        (product) =>
          product.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
          product.category.toLowerCase().includes(searchQuery.toLowerCase())
      )
    );
  }, [searchQuery, products]);

  const handleDelete = (productId) => {
    if (window.confirm("Are you sure you want to delete this product?")) {
      axios
        .delete(`http://192.168.1.40/api/products/${productId}`)
        .then(() => {
          setProducts(products.filter((product) => product._id !== productId));
        })
        .catch((error) => console.error("Error deleting product:", error));
    }
  };

  const handleSearchChange = (e) => {
    setSearchQuery(e.target.value);
  };

  const handleViewMovements = (productId) => {
    navigate(`/product/${productId}/movements`);
  };

  return (
    <Container>
      {/* Titre */}
      <Typography variant="h4" gutterBottom sx={{ fontWeight: "bold", mt: 2 }}>
        Liste des Produits
      </Typography>

      {/* Barre de recherche */}
      <TextField
        label="Rechercher un produit..."
        variant="outlined"
        fullWidth
        value={searchQuery}
        onChange={handleSearchChange}
        sx={{ mb: 3 }}
      />

      {/* Bouton d'ajout */}
      <Box display="flex" justifyContent="flex-end" mb={2}>
        <Tooltip title="Ajouter un produit">
          <IconButton
            color="primary"
            component={Link}
            to="/add-product"
            sx={{ bgcolor: "primary.light", "&:hover": { bgcolor: "primary.main", color: "white" } }}
          >
            <AddCircleIcon fontSize="large" />
          </IconButton>
        </Tooltip>
      </Box>

      {/* Tableau des produits */}
      <TableContainer component={Paper} sx={{ borderRadius: "10px", overflow: "hidden" }}>
        <Table>
          <TableHead sx={{ bgcolor: "primary.main" }}>
            <TableRow>
              <TableCell sx={{ color: "white", fontWeight: "bold" }}>Nom</TableCell>
              <TableCell sx={{ color: "white", fontWeight: "bold" }}>Catgorie</TableCell>
              <TableCell sx={{ color: "white", fontWeight: "bold" }}>Quantit</TableCell>
              <TableCell sx={{ color: "white", fontWeight: "bold", textAlign: "center" }}>
                Actions
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {filteredProducts.map((product) => (
              <TableRow key={product._id} hover>
                <TableCell>{product.name}</TableCell>
                <TableCell>{product.category}</TableCell>
                <TableCell>{product.quantity}</TableCell>
                <TableCell sx={{ textAlign: "center" }}>
                  {/* Modifier */}
                  <Tooltip title="Modifier">
                    <IconButton
                      component={Link}
                      to={`/edit-product/${product._id}`}
                      sx={{ color: "info.main", "&:hover": { color: "info.dark" } }}
                    >
                      <EditIcon />
                    </IconButton>
                  </Tooltip>

                  {/* Supprimer */}
                  <Tooltip title="Supprimer">
                    <IconButton
                      onClick={() => handleDelete(product._id)}
                      sx={{ color: "error.main", "&:hover": { color: "error.dark" } }}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </Tooltip>

                  {/* Voir les mouvements */}
                  <Tooltip title="Voir les mouvements">
                    <IconButton
                      onClick={() => handleViewMovements(product._id)}
                      sx={{ color: "secondary.main", "&:hover": { color: "secondary.dark" } }}
                    >
                      <VisibilityIcon />
                    </IconButton>
                  </Tooltip>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Container>
  );
};

export default ProductList;
