import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Container, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { Edit, Delete } from '@mui/icons-material';

const StockMovementList = () => {
    const [stockMovements, setStockMovements] = useState([]);
    const navigate = useNavigate();

    useEffect(() => {
        axios.get('http://192.168.1.40/api/mvt')
            .then(response => setStockMovements(response.data))
            .catch(error => console.error('Error fetching stock movements:', error));
    }, []);

    const handleDelete = (id) => {
        axios.delete(`http://192.168.1.40/api/mvt/${id}`)
            .then(() => {
                setStockMovements(stockMovements.filter(movement => movement._id !== id));
            })
            .catch(error => console.error('Error deleting stock movement:', error));
    };

    return (
        <Container>
            <Typography variant="h4" gutterBottom>Stock Movements</Typography>
            
            <Button variant="contained" color="primary" component={Link} to="/add-stockmovement" sx={{ mb: 2 }}>
                Add Movement
            </Button>

            <TableContainer component={Paper}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell><strong>Product</strong></TableCell>
                            <TableCell><strong>Type</strong></TableCell>
                            <TableCell><strong>Quantity</strong></TableCell>
                            <TableCell><strong>Location</strong></TableCell>
                            <TableCell><strong>Operator</strong></TableCell>
                            <TableCell><strong>Datetime</strong></TableCell>
                            <TableCell><strong>Actions</strong></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {stockMovements.map((movement) => (
                            <TableRow key={movement._id} sx={{ backgroundColor: movement.type === 'entree' ? '#d4edda' : '#f8d7da' }}>
                                <TableCell>{movement.product?.name || 'Unknown'}</TableCell>
                                <TableCell>{movement.type}</TableCell>
                                <TableCell>{movement.quantity}</TableCell>
                                <TableCell>{movement.location}</TableCell>
                                <TableCell>{movement.operator}</TableCell>
                                <TableCell>{new Date(movement.datetime).toLocaleString()}</TableCell>
                                <TableCell>
                                    <Button 
                                        variant="contained" 
                                        color="primary" 
                                        size="small" 
                                        onClick={() => navigate(`/edit-stockmovement/${movement._id}`)}
                                        sx={{ mr: 1 }}
                                    >
                                        <Edit fontSize="small" />
                                    </Button>
                                    <Button 
                                        variant="contained" 
                                        color="error" 
                                        size="small" 
                                        onClick={() => handleDelete(movement._id)}
                                    >
                                        <Delete fontSize="small" />
                                    </Button>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </Container>
    );
};

export default StockMovementList;
